export const fetchPizzas = (category, sortType, sortOrder) => (dispatch) => {
  let filteredPizzas;
  dispatch(setLoaded(false));

  setTimeout(() => {
    if (category === null) filteredPizzas = pizzas.pizzas;
    else {
      filteredPizzas = pizzas.pizzas.filter((item) => item.category === category);
    }
    if (sortOrder === 'asc') {
      console.log(sortType);
      filteredPizzas.sort((a, b) => a[sortType] - b[sortType]);
      console.log(filteredPizzas);
    } else {
      console.log(sortType);
      filteredPizzas.sort((a, b) => b[sortType] - a[sortType]);
    }

    dispatch(setPizzas(filteredPizzas));
    dispatch(setLoaded(true));
  }, 1000);
};

export const setPizzas = (items) => {
  console.log(items);
  return { type: 'SET_PIZZAS', payload: items };
};
